import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton  } from 'react-mdl';



class Projects extends Component {
 constructor(props) {
     super(props);
     this.state = { activeTab: 0 };
 }

toggleCategories(){
 if(this.state.activeTab === 0){
     return (<div className="projects-grid">
        
<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
<CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/png;base64,) center / cover'}}> Food Recipe App #1</CardTitle>
<CardText> A simple App which makes a request to an API when a user enters a name of a meal and the response from the API containig the recipe for the meal and other related meals each with its own recipe is displayed back to the User </CardText>
    <CardActions border>
        <Button onClick={()=>window.open("www.github.com/eseneose/food-recipe")} colored>Github</Button>
        <Button colored>CodePen</Button>
        <Button colored>LiveDemo</Button>
        </CardActions>
<CardMenu style={{color: '#fff'}}>
    <IconButton name="share"/>
</CardMenu>

</Card>



<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
<CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/png;base64,) center / cover'}}> React Project #2</CardTitle>
<CardText> loren ipsum gipsum distriu dkja asjjdj </CardText>
    <CardActions border>
        <Button colored>Github</Button>
        <Button colored>CodePen</Button>
        <Button colored>LiveDemo</Button>
        </CardActions>
<CardMenu style={{color: '#fff'}}>
    <IconButton name="share"/>
</CardMenu>

</Card>



<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
    <CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/png;base64,) center / cover'}}> React Project #3</CardTitle>
    <CardText> loren ipsum gipsum distriu dkja asjjdj </CardText>
        <CardActions border>
            <Button colored>Github</Button>
            <Button colored>CodePen</Button>
            <Button colored>LiveDemo</Button>
        </CardActions>
    <CardMenu style={{color: '#fff'}}>
        <IconButton name="share"/>
    </CardMenu>

</Card>
   </div>  )
 
} else if(this.state.activeTab === 1){
    return (
        <div><h1> no anguar projects yet </h1></div>
    )
} else if(this.state.activeTab === 2){
    return (
        <div><h1> no vueJS projects yet </h1></div>
    )
} else if(this.state.activeTab === 3){
    return (
        <div><h1> no python projects yet</h1></div>
    )
}
}
    render() {
        return(
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId})} ripple>
                    <Tab>React </Tab>
                    <Tab>Angular </Tab>
                    <Tab>vueJS </Tab>
                    <Tab>Python</Tab>
                </Tabs>


    <Grid>
        <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
        </Cell>
    </Grid>



            </div>
        )
    }
}
export default Projects;